<script setup>
    import { ref, onMounted } from "vue"
    import co2ds from "../../services/CO2DataService.js"

    const co2data = ref([0])

    const retrieveQuestion = () => {
        co2ds.getAll()
        .then(response => {
            co2data.value = response.data;
        //   console.log(response.data);
          console.log(co2ds.value)
        })
        .catch(e => {
          console.log(e);
        });
    }

    onMounted(() => {
        retrieveQuestion()
    })
    
</script>

<template>
    <div id="co2table">
        <table class="table table-striped">
            <thead>
            <tr>
                <th>측정날짜</th>
                <th>CO2</th>
            </tr>
        </thead>
        <tbody>
            <tr v-for="(co2ele, idx) in co2data" :key="idx">
                <td>{{ co2ele.mmdate }}</td>
                <td>{{ co2ele.co2 }}</td>
            </tr>
            
            <!-- <tr th:each="question : ${questionList}">
                <td th:text="${question.subject}"></td>
                <td th:text="${question.createDate}"></td>
            </tr> -->
        </tbody>
        </table>
    </div>
</template>

<style>
#co2table {
    width: 100%;
    height: 100%;
    background-color: aqua;
}
</style>